//darkmode checking
import useDarkMode from "use-dark-mode";

//nextui
import { Link } from "@nextui-org/react";

//internal
import ModalButton from "./FormModal";



export default function Footer(){

  let logoSrc;

  const darkMode = useDarkMode(false);
  const isdark = darkMode.value;

  //check if darkmode in change logo color
  if(isdark){
    logoSrc = "assets/images/logos/logo-wite.png";
  } else {
    logoSrc = "assets/images/logos/logo-black.png";
  }

  const footerMenu = [

    {id:1, title:" ",route:"/"},
    {id:2, title:"  ",route:"/"},
    {id:3, title:" ",route:"/"},
    {id:4, title:" ",route:"/"},
    {id:5, title:" ",route:"/"},

  ];
  return(
    <footer className="w-full mt-28">
      <div className="flex flex-col gap-6 justify-center items-center">
        {/* Logo */}
        <img src={logoSrc} className="transition-all w-[240px]" alt=" " />
        <span className="text-xl font-PinarMedium text-center">         </span>
        <ModalButton title=" " className="!bg-MainColor-10 !text-MainColor-50" />
        {/* Footer Menu */}
        <div className="flex flex-col lg:flex-row justify-center items-center text-xl font-PinarMedium gap-8">
        {
          footerMenu.map(menuitem=> 
          <Link
          className="!text-black dark:!text-white hover:!text-MainColor-50 transition-colors"
          href={menuitem.route}
          >
          {menuitem.title}
          </Link>
          )
        }
        </div>
        <div className="w-full flex flex-col lg:flex-row justify-center items-center gap-5 lg:justify-between dark:bg-MainColor-50 bg-MainColor-10 text-black font-PinarSemiBold py-4 lg:px-20 mt-6">
          <span>         </span>
          <span> : 09035557992</span>
        </div>
      </div>
    </footer>
  );
}