//nextui
import {Navbar, NavbarBrand, NavbarContent, NavbarItem, NavbarMenuToggle, NavbarMenu, NavbarMenuItem, Link, Button, Image, Switch} from "@nextui-org/react";
//raect
import { useState, useEffect } from "react";
//icon
import { MoonIcon, SunIcon } from "@heroicons/react/24/outline"
//darkmode
import useDarkMode from "use-dark-mode";

function ThemeSwitch(){
  const darkMode = useDarkMode(false);
  const isdark = darkMode.value;
  return(
    <Switch
    isSelected={isdark}
    size="lg"
    color="primary"
    startContent={<MoonIcon strokeWidth={2} />}
    endContent={<SunIcon strokeWidth={2} />}
    onChange={darkMode.toggle}
  >
  </Switch>
  );
}
  

export const Header = () => {

  //header postion
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 1) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  //usestate
  const [isMenuOpen, setIsMenuOpen] = useState(false);


  //menuitem
  const menuItem = [
    { title: "  ", rotue: "/contact" },
    { title: " ", rotue: "/portfolio" },
    { title: " ", rotue: "/about" },
    { title: "", rotue: "/blog" }
  ]

  //logo
  const logo = "assets/images/logos/fave-icon.png";

  return(
    <Navbar maxWidth="full" className={`py-6 lg:w-[95%] lg:rounded-[20px]  mx-auto fixed lg:top-4 md:bg-white/0  transition-colors ${scrolled ? 'shadow-small dark:shadow-sm dark:shadow-white/10 !bg-white/80 dark:!bg-black/80 !backdrop-blur-lg' : 'md:!backdrop-blur-0'}`} onMenuOpenChange={setIsMenuOpen}>
      <NavbarContent>
        <NavbarMenuToggle
          aria-label={isMenuOpen ? "Close menu" : "Open menu"}
          className="md:hidden"
        />
        <NavbarBrand className="justify-end md:justify-start">
          <Link href="/">
            <Image src={logo} className="w-[86px]" />
          </Link>
        </NavbarBrand>
      </NavbarContent>
      {/* Dekstop Menu */}
      <NavbarContent className="hidden md:flex gap-4" justify="center">
          {
            menuItem.map(item=> 
              <NavbarItem>
                <Link className={`text-white hover:border-b hover:pb-2 border-MainColor transition-all font-medium ${scrolled ? 'text-black dark:text-white' : ''}`} href={item.rotue}>{item.title}</Link>
              </NavbarItem>
            )
          }
      </NavbarContent>
      {/* Contact Button */}
      <NavbarContent justify="end" className="hidden md:flex">
        <Button as={Link} href="tel:09035557992" variant="flat" className="font-PinarSemiBold bg-MainColor text-black">
            
        </Button>
        <ThemeSwitch/>
      </NavbarContent>
      {/* Mobile Menu */}
      <NavbarMenu className="bg-black/70 space-y-5 pt-16">
      {
        menuItem.map(item=> 
          <NavbarMenuItem>
            <Link className="text-white" href={item.rotue}>{item.title}</Link>
          </NavbarMenuItem>
        )
      }
      <ThemeSwitch/>
      </NavbarMenu>
    </Navbar>
  );
}
export default Header;