//nextui
import { Button } from "@nextui-org/react";

export default function AboutUs(){
  const data = {
    title : "  ",
    subtitle : "                                                                                          ",
    image : "assets/images/video.png"
  }
  return(
    <section className="BaseContainer grid lg:grid-cols-2 gap-5" data-aos-once="true" data-aos="fade-down" data-aos-duration="1500">
      <div className="flex flex-col items-start gap-5">
        <span className="font-MorabbaSemiBold text-3xl text-MainColor-50">{data.title}</span>
        <p  className="font-PinarMedium text-justify text-lg">{data.subtitle}</p>
        <Button color="primary" className="text-black font-PinarSemiBold self-end"> </Button>
      </div>
      <div>
        <img  className="mr-auto w-full lg:w-auto lg:h-[300px]" src={data.image} alt="" />
      </div>
    </section>
  );
}