// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';

import { EffectCoverflow } from 'swiper/modules';




export function MySwiper({items}) {
  return (
    <>
        <Swiper
        effect={'coverflow'}
        grabCursor={true}
        centeredSlides={true}
        loop={true}
        breakpoints={{
          300 : {slidesPerView : 1.5},
          576 : {slidesPerView : 2},
          1200 : {slidesPerView : 3},
          1600 : {slidesPerView : 4},
        }}
        coverflowEffect={{
          rotate : 0,
          modifier: 7,
        }}
        modules={[EffectCoverflow,]}
        className="mySwiper"
      >
        {
          items.map(item=><SwiperSlide className='!rounded-xl overflow-hidden'>{item}</SwiperSlide>)
        }
      </Swiper>
    </>
  );
}



const Portfolio = ({title,url,image}) => {

  return(
    <div className="h-[340px] lg:h-[450px] !rounded-2xl overflow-hidden shadow-lg">
      {/* Header */}
      <div className="bg-Gray-10 w-full p-2 flex justify-between items-center gap-2">
        {/* Title */}
        <span className="w-[80%] block p-2 bg-Gray-20 text-black/40 rounded-full"> {title}</span>
        {/* Colors */}
        <div className="flex gap-2">
          <span className="w-4 h-4 bg-MainColor-50 rounded-full"></span>
          <span className="w-4 h-4 bg-MainColor rounded-full"></span>
          <span className="w-4 h-4 bg-black rounded-full"></span>
        </div>
      </div>
      <img src={image} alt={title} />
    </div>
  );
}


