import { useState } from "react";
import { Modal, ModalContent, ModalBody, Button, Input } from "@nextui-org/react";

//darkmode
import useDarkMode from "use-dark-mode";


const FormModal = ({ visible, onClose }) => {
  const darkMode = useDarkMode(false);

  return (
    <Modal backdrop="blur" isOpen={visible} onClose={onClose} className={`${darkMode.value ? 'dark' : ''} text-foreground dark:bg-zinc-950 py-4`}>
      <ModalContent>
        <div className="w-full flex flex-col items-stretch justify-center gap-5">
          <span className="mx-auto font-Morabba text-3xl text-MainColor-50">  </span>
          <ModalBody>
            <Input size="lg" type="text" variant="flat" placeholder="   " fullWidth/>
            <Input size="lg" type="tel" variant="flat" placeholder="" dir="rtl" fullWidth/>
            <Input size="lg" type="text" variant="flat" placeholder=" " fullWidth/>
            <Button size="lg" className="text-black font-PinarSemiBold" color="primary"> </Button>
          </ModalBody>
        </div>
      </ModalContent>
    </Modal>
  );
};

// Form Modal Button
const ModalButton = ({title,className}) => {
    const [visible, setVisible] = useState(false);
  
    const openModal = () => setVisible(true);
    const closeModal = () => setVisible(false);
  
    return (
      <div>
        <Button className={className} onPress={openModal}>
          {title}
        </Button>
        <FormModal visible={visible} onClose={closeModal} />
      </div>
    );
  };
  

export default ModalButton;
