//internal
import BaseLayout from '../Layout/BaseLayout';
import TopBar from "../Components/TopBar"

//nextui
import {Autocomplete, AutocompleteItem, Link} from "@nextui-org/react";

//darkmode
import useDarkMode from "use-dark-mode";


function PortfolioPageHeader(){
  const data = {
    title : "   ",
    subtitle : "                                                "
  }
  return(
    <section className='BaseContainer flex flex-col gap-5 justify-center items-center'>
      <h2 className='text-MainColor-50 font-MorabbaMedium text-3xl text-center'>{data.title}</h2>
      <p className='text-center font-PinarMedium text-lg mb-8'>{data.subtitle}</p>
    </section>
  );
}


function PortfolioPageFilter(){
    const darkMode = useDarkMode(false);

    const Categorys = [
      {label: "", value: "shop"},
      {label: "", value: "corprate"},
      {label: "", value: "service"},
      {label: "", value: "rest"},
    ]
    return(
      <section className='BaseContainer flex flex-col gap-5 justify-center items-center'>
        <Autocomplete
        size='lg'
        placeholder="   "
        classNames={{
          popoverContent:`${darkMode.value ? 'dark' : ''} text-foreground dark:bg-zinc-900`
        }}
        >
          {
            Categorys.map
            (
              category=>
              <AutocompleteItem key={category.value}>{category.label}</AutocompleteItem>
            )
          }
        </Autocomplete>
      </section>
    );
  }

  function PortfolioItem({title,image,url,category}){
    return(
      <Link isExternal href={url} className='rounded-2xl relative overflow-hidden'>
        <img src={image} alt={title} className='w-full h-auto' />
        <div className='bg-MainColor-10/90 text-MainColor-50 rounded-b-2xl absolute bottom-0 left-0 right-0 w-full mx-auto p-4 flex items-center justify-center backdrop-blur-sm font-PinarBold'>
          {title}
        </div>
      </Link>
    );
  }

  function PortfolioSitList(){
    const siteList = [
      {
        id:1,
        title : " ",
        url : "https://shalbaf-saffron.ir/",
        image : "assets/images/portfolio/np1.webp"
      },
      {
        id:2,
        title :"   ",
        url : "https://itsilver.ir",
        image : "assets/images/portfolio/np2.webp"
      },
      {
        id:3,
        title : "23 ",
        url : "https://23gallery.ir",
        image : "assets/images/portfolio/np3.webp"
      },
      {
        id:4,
        title : " ",
        url : "https://tenstore.ir",
        image : "assets/images/portfolio/np4.webp"
      },
      {
        id:4,
        title : " ",
        url : "https://tenstore.ir",
        image : "assets/images/portfolio/np4.webp"
      },
      {
        id:4,
        title : " ",
        url : "https://tenstore.ir",
        image : "assets/images/portfolio/np4.webp"
      },
    ]
    return(
      <section className='BaseContainer grid md:grid-cols-2 lg:grid-cols-3 gap-8'>
        {
          siteList.map(site=> <PortfolioItem title={site.title} key={site.id} image={site.image} url={site.url} />)
        }
      </section>
    );
  }




const PortfolioPage = () => {
  return(
    <BaseLayout props="!space-y-[52px] ">
    <TopBar title=" " />
    <PortfolioPageHeader />
    <PortfolioPageFilter />
    <PortfolioSitList />
    </BaseLayout>
  );
}
 
export default PortfolioPage;