//import Marquee
import Marquee from "react-easy-marquee";

function Services({ title, imgSrc }) {
  return (
    <div 
    className="z-10 m-20 w-[320px] h-[320px] shadow-md bg-MainColor-10/30 dark:bg-MainColor/30 rounded-3xl p-8 flex flex-col justify-center items-center transform even:scale-110 even:rotate-6 odd:scale-110 odd:-rotate-6 hover:transform-none transition-transform">
      <img className="w-[224px] h-[224px]" src={imgSrc} alt={title} />
      <span className="font-MorabbaSemiBold text-MainColor-50 dark:text-white text-2xl">{title}</span>
    </div>
  );
}

export const OurService= () => {
  const data = {
    title: "  ",
    subtitle: "                                                                                          "
  }
  const servicesData = [
    {
      id: 1,
      img: "assets/images/vectors/service-1.svg",
      title: " "
    },
    {
      id: 2,
      img: "assets/images/vectors/service-2.svg",
      title: " "
    },
    {
      id: 3,
      img: "assets/images/vectors/service-3.svg",
      title: " "
    },
    {
      id: 1,
      img: "assets/images/vectors/service-1.svg",
      title: " "
    },
  ];

  return (
    <div
    data-aos-once="true" 
    data-aos="fade-down"
    data-aos-duration="1500"
    className="flex flex-col gap-4"
    >
      <div className="BaseContainer flex flex-col gap-4 items-start">
      <span className="font-MorabbaSemiBold text-MainColor-50 text-3xl">{data.title}</span>
      <span className="font-PinarMedium text-lg text-justify">{data.subtitle}</span>
      </div>
      <Marquee 
      height={400}
      duration={30000}
      className="p-100"
      >
        {
          servicesData.map(service =>
            <Services key={service.id} title={service.title} imgSrc={service.img} />
          )
        }
      </Marquee>
    </div>
  );
}
export default OurService;